import { useEffect, useState } from 'react'
import { useParams } from "react-router";
import { connect } from 'react-redux';

import walletActions from '../../reducers/wallet/actions'

import {
  Box,
  Container,
  Typography,
} from '@mui/material';

import { withStyles } from '@mui/styles';

import { buyRole } from '../../api';
import ConnectWalletNavBar from '../../components/Header/ConnectWalletNavBar';
import Loading from "../../components/Loading";

// Toastr Message
import notify from '../../helpers/notify';
import { ToastContainer } from 'react-toastify'
// Toastr Message

import { accountAddress, initWallet, web3Modal } from "../../helpers/web3/Wallet";

import { contractABI, contractAddress, contractAddress_1, BronzeId, SilverId, GoldId, DiamondId, PublicId } from '../../config'

const styles = {
  formControl: {
    marginBottom: '20px'
  },

  card: {
    minWidth: '200px',
    maxWidth: '350px',
    display: 'flex',
    justifyContent: 'center'
  },

  noRole: {
    fontSize: '30px'
  }
}

function BuyRole(props) {
  // const data = { "roles": [{ "id": "910451180457586709", "name": "Administrator", "permissions": "1099511103487", "position": 4, "color": 15742004, "hoist": false, "managed": false, "mentionable": false, "icon": null, "unicode_emoji": null, "price": "12" }, { "id": "911554332246302740", "name": "new role", "permissions": "1071698660929", "position": 1, "color": 0, "hoist": false, "managed": false, "mentionable": false, "icon": null, "unicode_emoji": null, "price": null }] }

  // const [loading, setLoading] = useState(false);
  // const [roles, setRoles] = useState(data.roles);

  const [loading, setLoading] = useState(true);
  const [roles, setRoles] = useState([]);

  const { guildId, userId } = useParams();

  const handleChangeWalletAddress = async (address) => {
    props.changeWalletAddress(address)
  }

  const init = async () => {
    await initWallet()
    handleChangeWalletAddress(accountAddress)
  }

  useEffect(async () => {
    if (localStorage.getItem("WEB3_CONNECT_CACHED_PROVIDER")) {
      if (props.address !== '') {
        let chainId = await web3Modal.eth.getChainId()
        if (chainId != 250) {
          notify('error', 'Please switch to Fantom Opera and try again.');
          return;
        }
        const ShroomsContract = new web3Modal.eth.Contract(contractABI, contractAddress)
        const ShroomsContract_1 = new web3Modal.eth.Contract(contractABI, contractAddress_1)
        let balance = await ShroomsContract.methods.balanceOf(props.address).call()
        let balance_1 = await ShroomsContract_1.methods.balanceOf(props.address).call()
        let totalBalance = Number(balance) + Number(balance_1)
        let requestId = PublicId;
        let title = 'NOT'
        if (totalBalance >= 10) { requestId = DiamondId; title = 'Diamond'; }
        else if (totalBalance >= 6) { requestId = GoldId; title = 'Gold'; }
        else if (totalBalance >= 3) { requestId = SilverId; title = 'Silver'; }
        else if (totalBalance >= 1) { requestId = BronzeId; title = 'Bronze'; }
        buyRole(guildId, userId, requestId).then((res) => {
          console.log(res.data)
          setLoading(false)
          if (res.data.result == true)
            notify('success', `You are ${title} holder now.`)
          else
            notify('error', 'Role assigning failed')
        }).catch(err => {
          setLoading(false)
          notify('error', "Missing Permission")
          console.log(err)
        })
      }
    } else {
      setLoading(false)
      handleChangeWalletAddress('')
    }
  }, [props.address])

  useEffect(() => {
    console.log("init")
    init()
  }, [])

  return (
    <>
      {loading === true ? (
        <Loading />
      ) : (
        <>
          <div>
            <ToastContainer />
            <Container maxWidth="lg" >

            </Container>
          </div>
        </>
      )
      }
    </>
  );
}

const mapStateToProps = state => {
  return {
    address: state.wallet.address
  }
}

const mapDispatchToProps = (dispatch) => {
  return {
    changeWalletAddress: (address) => {
      dispatch({ type: walletActions.CHANGE_WALLET, payload: address })
    }
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(withStyles(styles)(BuyRole))